import React from "react";
import serviceIcon from "./assets/service-icon.png";
import "./styles.scss";

// sub components
import ServiceItem from "./components/ServiceItem";
export default function OurServices({}) {
  return (
    <div className="OurServices" id="services">
      <div className="OurServices__content">
        <h2>Nuestro servicios</h2>
        <div className="OurServices__list">
          <ServiceItem
            title="Marketing Digital"
            description="Ecommerce avanzado, planificación y gestión de etiquetas, pruesta test a/b"
          />
          <ServiceItem
            title="Diseño Ux / Ui"
            description="Creamos experiencias digitales que conectan con tus usuarios"
          />
          <ServiceItem
            title="Desarrollo"
            description="Llevamos tu diseño a la realidad con el mejor performance; web, android, iOS"
          />
          <ServiceItem
            title="Analítica"
            description="Enfocados en la recopilación, medición, análisis, visualización e interpretación de datos digitales"
          />
        </div>
      </div>
    </div>
  );
}
