import React from "react";
import "./styles.scss";

export default function Contact({}) {
  return (
    <div className="Contact" id="contact">
      <div className="Contact__content">
        <h2>Conversemos</h2>
        <h3>Cuéntanos sobre tu proyecto</h3>
        <form action="https://submit-form.com/ZItxcAcZ">
          <label>
            <span className="Contact__text">Nombres y apellidos</span>
            <input
              className="Contact__input"
              type="text"
              name="fullname"
              id="fullname"
              required
            />
          </label>
          <label>
            <span className="Contact__text">Correo electrónico</span>
            <input
              className="Contact__input"
              type="email"
              name="email"
              id="email"
              required
            />
          </label>
          <label>
            <span className="Contact__text">Celular</span>
            <input
              className="Contact__input"
              type="tel"
              name="telephone"
              id="telephone"
              required=""
            />
          </label>
          <label>
            <span className="Contact__text">Empresa (opcional)</span>
            <input
              className="Contact__input"
              type="text"
              name="company"
              id="company"
              required=""
            />
          </label>
          <label>
            <span className="Contact__text">Mensaje</span>
            <textarea
              className="Contact__input Contact__input--message"
              name="message"
              id="message"
              rows="5"
              required
            />
          </label>
          <button className="Contact__submitButton" type="submit">
            Enviar mensaje
          </button>
        </form>
      </div>
    </div>
  );
}
