import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "../../utils/fontawesome";

import logo from "./assets/golive-logo.png";
import "./styles.scss";

export default function Index(props) {
  const [showMobileMenu, setShowMobileMenu] = useState(false);

  return (
    <header className="Header">
      <div className="Header__content">
        <div className="Header__logo">
          <a href="/">
            <img src={logo} alt="GoLive Global" />
          </a>
        </div>
        <div className="Header__openMenu">
          <FontAwesomeIcon
            icon="bars"
            onClick={() => setShowMobileMenu(true)}
          />
        </div>

        <nav className={"Header__nav " + (showMobileMenu ? "is-visible" : "")}>
          {showMobileMenu && (
            <FontAwesomeIcon
              icon="times"
              className="Header__closeButton"
              onClick={() => setShowMobileMenu(false)}
            />
          )}
          <ul>
            {/* <li>
              <a href="#process">Proceso</a>
            </li>
            <li>
              <a href="#about">Qué dicen</a>
            </li> */}
            <li>
              <a href="#services">Servicios</a>
            </li>
            <li>
              <a href="#contact">Contacto</a>
            </li>
          </ul>
        </nav>
      </div>
    </header>
  );
}
